/* ---------------------------------- */
/* General Styles
------------------------------------- */
input, input[type="text"], input[type="password"], input[type="email"], input[type="number"], textarea, select {
	height: 48px;
	line-height: 48px;
	padding: 0 15px;
	outline: none;
	font-size: 14px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fbfdff;
	font-weight: 500;
	opacity: 1;
	border-radius: 4px;
	border: none;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.12);
}
select {
	padding: 15px 18px;
	cursor: pointer;
}
input {
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	-o-transition: all 0.1s ease-in-out;
	-ms-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}
input:focus, input[type="text"]:focus, input[type="password"]:focus, input[type="email"]:focus, input[type="number"]:focus, textarea:focus {
	color: #808080;
	transition: box-shadow 0.2s !important;
	opacity: 1;
	border: none;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.12);
}
 input:-webkit-autofill:focus, input:-webkit-autofill {
 -webkit-box-shadow: 0 0 0 30px #fff inset !important;
 -webkit-text-fill-color: #808080 !important;
}
input.utf-with-border, input[type="text"].utf-with-border, input[type="password"].utf-with-border, input[type="email"].utf-with-border, input[type="number"].utf-with-border, textarea.utf-with-border, select.utf-with-border {
	background:#fbfdff;
	border: 1px solid #dde6ef;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.05);
}
input.utf-with-border:focus, input[type="text"].utf-with-border:focus, input[type="password"].utf-with-border:focus, input[type="email"].utf-with-border:focus, input[type="number"].utf-with-border:focus, textarea.utf-with-border:focus {
	border: 1px solid #e0e0e0;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.08);
}
input[type="submit"] {
	border: none;
	padding: 11px 18px;
	width: auto;
}
input[type="checkbox"] {
	display: inline;
}
input[type="radio"] {
	width: 15px;
	height: 15px;
	cursor: pointer;
	box-shadow: none;
}
::placeholder {
	color: #808080;
	opacity: 1;
}
textarea {
	height: auto;
	line-height: 27px;
	padding:15px 20px 20px 20px;
	min-height: 130px;
	transition: none !important;
	min-width: 100%;
}
label, legend {
	display: block;
	font-weight: bold;
	font-size: 16px;
	font-weight: normal;
	margin-bottom: 8px;
}
label span, legend span {
	font-weight: normal;
	font-size: 14px;
	color: #444;
}
fieldset {
	padding: 0;
	border: none;
}
button {
	border: none;
	margin: 0;
	padding: 0;
	width: auto;
	overflow: visible;
	background: transparent;
	color: inherit;
	font: inherit;
	line-height: normal;
	cursor: pointer;
}
a {
	color: #66676b;
	transition: 0.3s;
}
a, button {
	outline: none !important;
}
a:focus, a:hover {
	text-decoration: none;
	color: #333;
}
img {
	max-width: 100%;
}
.text-center{
	text-align:center;
}
/* ---------------------------------- */
/* Header
------------------------------------- */
#logo {
	display: inline-block;
	height: 100%;
	vertical-align: top;
	padding-right: 30px;
	margin-right: 20px;
}
#logo img {
	height: 44px;
	width: auto;
	top: 50%;
	position: relative;
	transform: translate3d(0, -50%, 0);
}
#header {
	position: relative;
	z-index: 999;
	padding: 0;
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.09);
	font-size: 16px;
	height: 82px;
	background-color: #fff;
}
#header .container, #header .utf-right-side, #header .utf-left-side {
	height: 100%;
}
header.fullwidth .container {
	max-width: 100%;
	margin: 0;
	padding: 0 35px;
}
#header .container {
	position: relative;
}
#header .utf-left-side {
	float: left;
	width: 100%;
	display: inline-block;
	position: relative;
	flex: 1;
}
#header .utf-right-side {
	float: right;
	text-align: right;
	width: auto;
	display: inline-block;
	position: absolute;
	right: 25px;
	background-color: #fff;
	flex: 1;
}
.full-width #header .utf-left-side {
	float: left;
	width: 100%;
	display: inline-block;
	position: relative;
}
.full-width #header .utf-right-side {
	float: right;
	text-align: right;
	width: auto;
	display: inline-block;
	position: absolute;
	right: 25px;
	background-color: #fff;
}
/* Header Widget
------------------------------------- */
.utf-header-widget-item {
	display: inline-block;
	height: 100%;
	padding: 0 15px;
	text-align: left;
	float: left;
}
.fullwidth .utf-header-widget-item:last-of-type {
	padding-right: 5px;
	float: right;
	border-right: none;
}
.utf-header-widget-item:last-of-type {
	padding-right: 0;
	margin-right: -10px;
}
.fullwidth .utf-header-widget-item:last-of-type {
	padding-right: 15px;
	margin-right: 0;
}
.utf-header-notifications-content .notification-avatar {
	height: 42px;
	width: 42px;
	max-width: 42px;
	display: inline-block;
	border-radius: 50%;
	overflow: hidden;
}
.utf-header-widget-item .log-in-button {
	background:#00b4d8;
	position: relative;
	top: 50%;
	border-radius:40px;
	transform: translateY(-50%);
	transition: 0.3s;
	color: #ffffff;
	border:2px solid #00b4d8;
	padding:7px 15px;
	display: block;
	margin: 0 -3px;	
	min-width: 116px;
	text-align: center;
	line-height: 26px;	
}
.utf-header-widget-item .log-in-button:hover{
	background:transparent;
	border:2px solid #00b4d8;
}
body .utf-transparent-header-block .utf-header-widget-item .log-in-button{
	background:#ffffff;
	color:#00b4d8;
	border:2px solid #ffffff;
}
body .utf-transparent-header-block .utf-header-widget-item .log-in-button i{
	color:#00b4d8;
}
body .utf-transparent-header-block .utf-header-widget-item .log-in-button:hover i{
	color:#ffffff;
	background:transparent;
}
body .utf-transparent-header-block .utf-header-widget-item .log-in-button:hover{
	background:transparent;
	color:#ffffff;
	border:2px solid #ffffff;
}
.utf-header-widget-item .log-in-button i {
	font-size: 18px;
	line-height: 0;
	position: relative;
	top: 2px ;
	margin-right: 4px;
	margin-left: 0px;
	color: #ffffff;
	transition: 0.3s;
}
.utf-header-widget-item .log-in-button:hover, .utf-header-widget-item .log-in-button:hover i {
	color: #66676b;
}
.utf-transparent-header-block .utf-header-widget-item .log-in-button, .utf-transparent-header-block .utf-header-widget-item .log-in-button i {
	color: #fff;
}
@media (max-width: 767px) {
.utf-header-widget-item .log-in-button span {
	display: none;
}
.utf-header-widget-item .log-in-button i {
	font-size: 20px;
	top: 3px;
}
.utf-header-widget-item .log-in-button {
	border-radius: 40px;
	height: 44px;
	width: 44px;
	text-align: center;
	min-width: auto;
	padding: 0;
	line-height: 40px;
}
.utf-transparent-header-block .utf-header-widget-item .log-in-button i,  .utf-transparent-header-block .utf-header-widget-item .log-in-button {
	background-color: #fff;
	color: #00b4d8;
	border-color:#ffffff;
}
}
/* ---------------------------------- */
/* Header Notifications
------------------------------------- */
.utf-header-notifications {
	height: 100%;
	display: inline-block;
	padding-right: 15px;
	position: relative;
}
.utf-header-notifications:last-child {
	margin-right: -15px;
}
.utf-header-notifications-trigger, .utf-header-notifications-trigger a {
	font-size: 22px;
	color: #333;
	position: relative;
	top: 50%;
	transform: translate3d(0, -50%, 0);
}
.utf-header-notifications .notifications-trigger-icon{
	background:#eeeeee;
	border-radius:50px;
	width: 42px;
	height: 42px;
	text-align: center;
	top:50%;
}
.utf-header-notifications .notifications-trigger-icon a{
	top:24%;
	font-size:20px;
}
@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
.utf-header-notifications-trigger,  .utf-header-notifications-trigger a {
	height: 40px;
}
.utf-header-notifications.user-menu {
	top: -8px;
}
}
.utf-header-notifications-trigger span {
	position: absolute;
	display: inline-block;
	top: -15px;
	margin-left: -5px;
	font-weight: 700;
	height: 20px;
	width: 20px;
	line-height: 19px;
	text-align: center;
	color: #fff;
	font-size: 12px;
	background-color: #66676b;
	border-radius: 50%;
}
.utf-header-notifications-dropdown-block {
	width: 360px;
	border-radius: 4px;
	box-shadow: 0px 2px 12px 0px rgba(0,0,0,0.12);
	background-color: #fff;
	padding: 0;
	position: absolute;
	top: calc(100% - 10px);
	right: -15px;
	text-align: left;
	z-index: 100;
	transform: scale(0.95);
	opacity: 0;
	visibility: hidden;
	transition: 0.3s;
	pointer-events: none;
}
.utf-header-notifications.active .utf-header-notifications-dropdown-block {
	transform: scale(1);
	opacity: 1;
	visibility: visible;
	pointer-events: all;	
}
.utf-header-notifications-dropdown-block:before {
	content: "";
	position: absolute;
	right: 43px;
	top: -6px;
	width: 0;
	height: 0;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-bottom: 6px solid #ffffff;
	top: 0;
	right: 27px;
    border: 7px solid #ffffff;
    border-color: transparent transparent #00b4d8 #00b4d8;
    transform-origin: 0 0;
    transform: rotate(135deg);
    box-shadow: -3px 3px 3px 0 rgba(0, 0, 0, 0.05);
}
.utf-right-side .utf-header-notifications-dropdown-block:before {
	border-color: transparent transparent #ffffff #ffffff;    
}
.utf-header-notifications-headline {
	background:#00b4d8;
	padding: 18px 20px;
	padding-top: 18px;
	line-height: 24px;	
	border-radius:4px 4px 0 0;
}
.utf-header-notifications-headline h4, .utf-header-notifications-headline span {
	display: inline-block;
	color:#fff;
}
button.mark-as-read {
	background-color: #ffffff;
	float: right;
	position: relative;
	border-radius: 4px;
	height: 32px;
	width: 32px;
	text-align: center;
	line-height: 36px;
	top: -4px;
	right: -8px;
	color: #323232;
	transition: 0.25s;
}
.utf-header-notifications-content {
	display: block;
	width: 100%;
}
.utf-header-notifications-scroll {
	display: block;
	overflow: hidden;
	height: auto;
}
.utf-header-notifications-content ul {
	padding: 0;
	margin: 0;
	list-style: none;
}
.utf-header-notifications-content ul li {
	border-bottom: 1px solid #e6e6e6;
	transition: 0.25s;
}
.utf-header-notifications-content ul li:hover {
	background-color: #fafafa;
}
.utf-header-notifications-content ul li:hover .notification-icon {
	background-color: #eee;
}
.utf-header-notifications-content ul li:last-child {
	border-bottom: none;
}
.utf-header-notifications-content ul li a {
	color: #666;
	position: relative;
	padding:15px 12px;
	display: block;
}
.utf-header-notifications-content p {
	margin: 0;
	padding: 0;
}
.utf-header-notifications-content ul li a {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}
.utf-header-notifications-content .notification-icon, .utf-header-notifications-content .notification-text {
	flex: 1;
}
.utf-header-notifications-content .notification-icon i {
	position: relative;
}
.notification-icon, .utf-header-notifications-content .notification-icon {
	font-size: 18px;
	height: 38px;
	width: 38px;
	max-width: 38px;
	display: inline-block;
	background-color: #f2f2f2;
	border-radius: 50px;
	line-height: 40px;
	text-align: center;
	color: #a0a0a0;
	transition: 0.25s;
	position: relative;
}
.notification-icon .icon-material-outline-autorenew {
	font-size: 22px;
}
.notification-icon .icon-material-outline-gavel {
	font-size: 19px;
	top: -2px;
	left: 1px;
}
.utf-header-notifications-content .notification-avatar {
	height: 42px;
	width: 42px;
	max-width: 42px;
	display: inline-block;
	border-radius: 50%;
	overflow: visible;
	align-self: flex-start;
}
.utf-header-notifications-content .notification-avatar img {
	width: 100%;
	image-rendering: -webkit-optimize-contrast;
	border-radius: 50%;
}
.notification-text, .utf-header-notifications-content .notification-text {
	padding-left: 12px;
	padding-right: 0px;
	display: inline-block;
	line-height: 23px;
	font-size: 14.3px
}
.utf-header-notifications-content .notification-msg-text {
	display: inline-block;
	line-height: 23px;
	font-size: 14.7px;
	margin: 4px 0;
}
.notification-text strong, .utf-header-notifications-content .notification-text strong {
	font-weight: 600;
	color: #333;
}
.notification-text span.color, .utf-header-notifications-content .notification-text span.color {
	color: #66676b;
	display: inline-block;
}
.notification-text span.color {
	display: block;
}
a.utf-header-notifications-button {
	border-radius: 0 0 4px 4px;
	font-weight: 600;
	color: #fff;
	font-size: 14.7px;
	padding: 12px 16px;
	background-color: #66676b;
	width: 100%;
	display: block;
	text-align: center;
	transition: 0.3s;
}
a.utf-header-notifications-button i {
	font-size: 18px;
	line-height: 0;
	top: 3px;
	position: relative;
	max-width: 0;
	display: inline-block;
	margin-left: 7px
}
