}
ul.utf-user-menu-dropdown-nav li a {
	display: block;
	padding: 2px 0;
	font-size: 16px;
	color: #666;
	transition: 0.3s;
}
ul.utf-user-menu-dropdown-nav li a i {
	background:rgba(0, 0, 0, 0.06);
	font-size: 16px;
	position: relative;
	top: 1px;
	padding:6px;
	border-radius:30px;
	margin-right: 5px;
	color: #666;
	-webkit-font-smoothing: antialiased;
	transition: 0.3s;
}
ul.utf-user-menu-dropdown-nav li a:hover, ul.utf-user-menu-dropdown-nav li a:hover i {
	color: #66676b;
}
.user-status {
	padding:20px 15px;
	border-bottom: 1px solid #e6e6e6;
}
.user-details {
	display: flex;
}
.user-name {
	font-weight: 500;
	color: #333;
	line-height: 20px;
	padding: 2px 0 0 15px;
}
.user-profile-title .user-name {
	font-weight: 600;
	color: #666;
	line-height: 20px;
	padding: 2px 0px 0 3px;
	display:inline-block;
	font-size:16px;
}
.user-details span {
	display: block;
	font-size: 14.7px;
	color: #888;
	font-weight: 500;
}
body .utf-transparent-header-block .user-profile-title .user-name {
	color:#ffffff;
}
.status-switch {
	background-color: #eee;
	width: 100%;
	display: block;
	border-radius: 4px;
	height: 36px;
	padding: 0;
	position: relative;
	zoom: 1;
	margin-top: 20px;
}
.status-switch:before, .status-switch:after {
	content: " ";
	display: table;
}
.status-switch label {
	float: left;
	width: 50%;
	position: relative;
	z-index: 2;
	line-height: 36px;
	cursor: pointer;
	color: #888;
	font-size: 14.7px;
	text-align: center;
	transition: 0.3s;
	margin: 0;
}
.status-switch span.status-indicator {
	position: relative;
	z-index: 1;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	display: block;
	border-radius: 4px 0 0 4px;
	background-color: #38b653;
	box-shadow: 0 0 5px 1px rgba(56,182,83,0.3);
	transition: all 0.4s cubic-bezier(0.3, -.41, .19, 1.3), background-color 0.2s linear;
}
.status-switch span.right {
	left: 50%;
	border-radius: 0 4px 4px 0;
	background-color: #333;
	box-shadow: none;
	color: #fff;
}
.status-switch label.current-status {
	color: #fff;
}
/* ---------------------------------- */
/* Navigation
------------------------------------- */
#navigation {
	position: relative;
	display: inline-block;
	margin-top: 23px;
}
#navigation ul {
	list-style: none;
	position: relative;
	float: left;
	margin: 0;
	padding: 0;
}
#navigation ul a {
	display: block;
	text-decoration: none;
	padding: 5px 0;
}
#navigation ul li {
	position: relative;
	float: left;
	margin: 0;
	padding: 0
}
#navigation ul li ul {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: 100%;
	left: 9px;
	padding: 0;
	z-index: 99999;
}
#navigation ul ul li {
	float: none;
}
#navigation ul ul ul {
	top: 0;
	left: 100%;
	margin: 0 0 0 4px;
}
#navigation ul ul {
	margin: 0;
}
#navigation ul ul ul {
	pointer-events: none;
}
#navigation ul ul li:hover > ul {
	pointer-events: all;
}
#navigation ul ul:before {
	content: "";
	position: absolute;
	left: 0;
	top: -10px;
	background: rgba(255,255,255,0);
	width: 100%;
	height: 10px;
}
#navigation ul ul ul:after {
	content: "";
	position: absolute;
	width: 15px;
	height: 100%;
	left: -15px;
	top: 0;
	opacity: 0;
}
#navigation ul li:hover > ul {
	opacity: 1;
	visibility: visible;
	pointer-events: all;
}
#navigation ul li a:after, #navigation ul ul li a:after {
	font-family: "Material-Icons";
	content: '\e957';
	opacity: 1;
	font-size: 16px;
