/* ---------------------------------- */
/* Back to top
------------------------------------- */
#backtotop {
	position: fixed;
	right: 0;
	opacity: 0;
	visibility: hidden;
	bottom: 25px;
	margin: 0 25px 0 0;
	z-index: 999;
	transition: 0.35s;
	transform: translateY(10px);
}
#backtotop.visible {
	opacity: 1;
	visibility: visible;
	transform: translateY(0);
}
#backtotop a {
	text-decoration: none;
	border: 0 none;
	display: block;
	width: 42px;
	height: 42px;
	background-color: #66676b;
	opacity: 1;
	transition: all 0.3s;
	border-radius: 50px;
	text-align: center;
	font-size: 24px;
}
body #backtotop a {
	color: #fff;
}
#backtotop a:after {
	content: "\e910";
	font-family: "Feather-Icons";
	position: relative;
	display: block;
	top:17%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	transform: rotate(90deg);
}
