	background-color: rgba(0,0,0,0.25);
}
.enable-filters-button i {
	position: absolute;
	z-index: 90;
	left: -48px;
	height: 100%;
	font-size: 20px;
	background-color: #66676b;
	color: #fff;
	line-height: 48px;
	text-align: center;
	width: 48px;
