.list-apply-button {
	position: absolute;
	right: 0;
	padding: 10px 20px;
	line-height: 24px;
	position: relative;
	font-size: 16px;
	font-weight: 600;
	display: inline-block;
	transition: all 0.3s;
	border-radius: 4px;
	background: #00b4d8;
	color: #ffffff;
	box-shadow:0 2px 6px rgba(0,0,0,0.10);
	text-align: center;
}
.utf-job-listing.utf-apply-button-item:hover .list-apply-button {
	background-color: #66676b;
	color: #fff;
	box-shadow: 0 4px 12px rgba(102,103,107,0.15);
}
.list-apply-button i{
	background:#ffffff;
	border-radius:30px;
	padding:5px;
	font-size:14px;
	margin-left:3px;
	color:#00b4d8;
}
.utf-boxed-list-item .compact-list-layout {
	box-shadow: none;
	margin-top: 0;
	display:inline-block;
	width:100%;
}
@media (max-width: 768px) {
.list-apply-button {
	flex: auto;
	width: 100%;
	margin-top: 15px;
}
}
/* ---------------------------------- */
/* Job Listing Grid
------------------------------------- */
.grid-layout {
	display: flex;
	flex-wrap: wrap;
	width: calc(100% + 30px);
}
.grid-layout .utf-job-listing {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 0 30px 30px 0;
 width: calc(100% * (1/2) - 30px);
	flex-direction: column;
}
.grid-layout .utf-job-listing-details {
	flex-grow: 1;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	padding: 30px;
	padding-right: 40px;
}
@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
.tasks-list-container.tasks-grid-layout .task-listing .task-listing-details,  .grid-layout .utf-job-listing-details {
	flex-basis: 100px;
}
.tasks-list-container.tasks-grid-layout .task-listing-bid {
	flex-basis: 100px;
}
}
.grid-layout .utf-job-listing-footer {
	flex-grow: 0;
	padding: 20px 30px;
}
.grid-layout .utf-job-listing h3.utf-job-listing-title {
	font-size: 18px;
	line-height: 28px;
}
.grid-layout .utf-job-listing-footer .bookmark-icon {
	top: 0;
	right: 25px;
	transform: translateY(-50%) scale(0.96);
}
.grid-layout .utf-job-listing .utf-job-listing-company-logo {
	max-width: 56px;
	margin-right: 24px;
	top: 1px;
	flex: 0 0 56px;
}
@media (max-width: 768px) {
.full-page-content-inner .grid-layout .utf-job-listing,  .grid-layout .utf-job-listing {
	margin: 0 0 30px 0;
	width: 100%;
}
.grid-layout,  .full-page-content-inner .grid-layout {
	width: 100%;
}
.full-page-content-container .simplebar-content {
	overflow-x: hidden !important;
}
.full-page-container.with-map .full-page-content-inner .grid-layout .utf-job-listing {
	width: 100%;
}
}
/* ---------------------------------- */
/* Tasks - List Layout
------------------------------------- */
.task-listing {
	border-radius: 4px;
	box-shadow: 0 2px 12px rgba(0,0,0,0.12);
	margin-bottom: 30px;
	background-color: #fff;
	transition: 0.3s;
	position: relative;
	display: flex;
	justify-content: center;
	cursor: pointer;
}
.task-listing:hover {
	box-shadow: 0 2px 18px rgba(0,0,0,0.14);
	transform: translateY(-4px);
}
.task-listing-details {
	padding: 25px 30px;
	flex: 1;
}
.task-listing .task-listing-description {
	flex: 1;
	align-items: center;
	justify-content: center;
}
.task-listing .task-listing-description p {
	margin: 15px 0 0 0;
	padding: 0;
	color: #666;
}
.task-listing h3.task-listing-title a, .task-listing h3.task-listing-title {
	font-size: 18px;
	color: #333;
	line-height: 28px;
	cursor: pointer;
	font-weight: 600;
	padding-top: 2px;
	transition: 0.3s;
}
.task-listing .task-icons {
	list-style: none;
	padding: 0;
	margin: 3px 0 0 -3px;
}
.task-listing .task-icons li {
	background: #ffffff;
    display: inline-block;
    margin-right: 6px;
    margin-bottom: 5px;
    color: #8c8c8c;
    font-size: 14px;
    font-weight: 600;
    padding: 0px 8px;
    border-radius: 4px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.10);
}
.task-listing ul.task-icons li i.icon-material-outline-location-on, .task-listing ul.task-icons li i.icon-material-outline-location-off {
    color: #dc3139;
    font-size: 15px;
}
.task-listing ul.task-icons li i.icon-material-outline-access-time {
    color: #0bbdc6;
	font-size:15px;
}
.task-listing .task-icons i {
	color: #909090;
	position: relative;
	top: 1px;
}
.task-listing .task-icons i.icon-material-outline-desktop-windows {
	font-size: 19px;
	top: 2px;
}
.task-listing-bid {
	flex: 0 0 260px;
	background-color: #f8f8f8;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}
.task-listing-bid-inner {
	width: 100%;
	padding: 25px;
}
.task-listing-bid-inner .button {
	display: block;
	text-align: center;
	box-shadow: 0 4px 12px rgba(102,103,107,0.15);
	width: 100%;
}
.task-listing-bid-inner .button:hover {
	box-shadow: 0 4px 12px rgba(102,103,107,0.25);
}
.task-listing-bid-inner .task-offers i.icon-material-outline-account-balance-wallet {
    color: #2a41e8;
    padding-right: 3px;
}
.task-listing-bid .task-offers {
	margin-bottom: 15px;
	background: #ffffff;
	display: inline-block;
	width:100%;
	padding: 7px 10px;
	text-align:center;
	border-radius: 4px;
	box-shadow: 0 2px 6px rgba(0,0,0,0.10);
}
.task-listing-bid .task-offers strong {
	display: block;
	color: #333;
	font-weight: 600;
	line-height: 24px;
}
.task-listing-bid .task-offers span {
	display: block;
	color: #888;
	line-height: 24px;
}
.task-tags {
	display: block;
	margin: 23px 0 3px 0;
}
.task-tags span {
	transition: 0.3s;
	font-size: 16px;
	border-radius: 4px;
	background-color: rgba(102,103,107,.07);
	color: #66676b;
	display: inline-block;
	padding: 5px 15px;
	margin: 4px 2px;
	text-transform:capitalize;
	line-height: 24px;
}
.task-tags span:hover{
	background:#00b4d8;
	color:#ffffff;
}
.compact-list .task-listing {
	border-radius: 0;
	margin-bottom: 0;
	box-shadow: none;
}
.compact-list .task-listing:hover {
	transform: translateY(0);
}
.tasks-list-container.compact-list {
	background-color: #fff;
	border-radius: 4px;
	box-shadow: 0 2px 12px rgba(0,0,0,0.12);
}
.tasks-list-container.compact-list .task-listing {
	border-radius: 4px;
	box-shadow: 0 2px 12px rgba(0,0,0,.12);
	margin-bottom: 30px;
	background-color: #fff;
	transition: .3s;
	z-index: 1;
	position: relative;
	display: flex;
	border:1px solid transparent;
	overflow:hidden;
	float: left;
	width: 100%;
	justify-content: center;
	cursor: pointer;
}
.tasks-list-container.compact-list .task-listing:hover{
	transform: scale(1.02);
	overflow: hidden;
	border: 1px solid #00b4d8 !important;
	box-shadow: 0 1px 12px rgba(0,0,0,0.08);
}
.tasks-list-container.compact-list .task-listing-bid {
	background-color: transparent;
}
.tasks-list-container.compact-list .task-listing:hover:before {
	opacity: 1;
}
.tasks-list-container.compact-list .task-listing:before {
