/* ---------------------------------- */
/* Star Rating
------------------------------------- */
.utf-star-rating {
	display: inline-block;
}
.utf-star-rating .star {
	display: inline-block;
	margin: 0;
	padding: 0;
	top:-1px;
	float: left;
	margin-right: 0;
	position: relative;
}
.utf-star-rating .star.half:after, .utf-star-rating .star:before {
	font-family: "Material-Icons";
	content: "\e988";
	display: block;
	color: #00b4d8;
	font-size: 20px;
}
.utf-star-rating .star.empty:before {
	color: #ddd;
}
.rating:after {
	content: ".";
	display: block;
	clear: both;
	visibility: hidden;
	line-height: 0;
	height: 0;
}
.utf-star-rating .star.half:before {
	color: #ddd;
}
.utf-star-rating .star.half:after {
	color: #00b4d8;
	position: absolute;
	top: 0;
	width: 50%;
	display: block;
	height: 100%;
	overflow: hidden;
}
.utf-star-rating:before {
	content: attr(data-rating);
	float: right;
	border-radius: 4px;
	background-color: #38b653;
	color: #fff;
	font-size: 12px;
	line-height: 16px;
	font-weight: 700;
	padding: 2px 7px;
	position: relative;
	top: 4px;
	margin-left: 8px;
}
.utf-star-rating.no-stars .star {
	display: none;
}
.utf-star-rating.no-stars:before {
	margin-right: 0;
}
/* ---------------------------------- */
/* Leave Rating Stars
------------------------------------- */
.leave-rating {
