}
.utf-welcome-text-item .leave-rating-container {
	text-align: center;
	margin: 0 auto;
	margin-top: 0px;
	left: 50%;
	width: 100%;
	display: block;
}
.utf-welcome-text-item .leave-rating {
	position: relative;
	left: 50%;
	transform: translateX(-50%) scale(1.2);
}
/* ---------------------------------- */
/* Ripple Effect
------------------------------------- */
.utf-ripple-effect-dark, .ripple-effect {
	overflow: hidden;
