/* ---------------------------------- */
/* Radio Button
------------------------------------- */
.utf-radio-btn-list .radio{
	width:100%;
	padding-bottom:7px;
	margin-bottom: 8px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}
.utf-radio-btn-list .radio:last-child{
	padding-bottom:0;
	margin-bottom: 0;
	border-bottom:0;
}
.utf-radio-btn-list .checkbox{
	width:100%;
	padding-bottom:4px;
	margin-bottom: 8px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}
.utf-radio-btn-list .checkbox:last-child{
	padding-bottom:0;
	margin-bottom: 0;
	border-bottom:0;
}
.utf-radio-btn-list .checkbox label{
	margin-bottom:4px;
}
.radio {
	display: inline-block;
	vertical-align: top;
}
.radio label {
	margin: 3px 0;
	cursor: pointer;
	position: relative;
	padding-left: 29px;
	line-height: 25px;
}
.radio input[type="radio"] {
	position: absolute;
	opacity: 0;
}
.radio input[type="radio"] + label .radio-label {
	content: '';
	background: #fff;
	border-radius: 100%;
	border: 2px solid #b4b4b4;
	display: inline-block;
	width: 18px;
	height: 18px;
	position: relative;
	margin-right: 5px;
	vertical-align: top;
	cursor: pointer;
	text-align: center;
	transition: all 250ms ease;
	background-color: #fff;
	box-shadow: inset 0 0 0 8px #fff;
	z-index: 100;
	position: absolute;
	top: 2px;
	left: 0;
}
.radio input[type="radio"] + label .radio-label:after {
	background-color: #66676b;
	content: "";
	top: 3px;
	left: 0;
	right: 0;
	margin: 0 auto;
	position: absolute;
	opacity: 1;
	transition: 0.3s;
	border-radius: 4px;
	transform: scale(0);
	position: absolute;
	top: 0;
	left: 0;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	z-index: 99;
}
.radio input[type="radio"]:checked + label .radio-label {
	background-color: #66676b;
	border-color: #66676b;
	box-shadow: inset 0 0 0 3px #fff;
}
.radio-label:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	border-radius: 50%;
	background-color: transparent;
	display: block
}
.interactive-effect label .radio-label:before {
	animation: radius-shadow 0.4s;
}
 @keyframes radius-shadow {
 0% {
box-shadow: 0 0 0 0 rgba(102,103,107,0.4);
}
 100% {
box-shadow: 0 0 0 10px rgba(0,0,0,0);
}
}
.small-label {
	border-radius: 4px;
	display: inline-block;
	background: #e0f5d7;
	color: #449626;
	padding: 0 8px;
	line-height: 27px;
	height: 26px;
	margin-left: 5px;
	position: relative;
	top: -1px;
}
/* ---------------------------------- */
/* Checkbox
------------------------------------- */
.checkbox {
	display: inline-block;
	margin-bottom: 0;
}
.checkbox input {
	padding: 0;
	height: initial;
	width: initial;
	margin-bottom: 0;
	display: none;
	cursor: pointer;
}
.checkbox label {
	position: relative;
	cursor: pointer;
	padding-left: 30px;
	line-height: 25px;
}
.checkbox label span.checkbox-icon {
	content: '';
	-webkit-appearance: none;
	background-color: transparent;
	border: 2px solid #b4b4b4;
	height: 20px;
	width: 20px;
	display: inline-block;
	position: relative;
	vertical-align: middle;
	cursor: pointer;
	margin-right: 5px;
	border-radius: 40px;
	transition: 0.3s;
	position: absolute;
	left: 0;
	top: 2px;
}
.checkbox input + label span.checkbox-icon:after {
	position: absolute;
	top: 2px;
	left: 0;
	right: 0;
	margin: 0 auto;
	opacity: 0;
	content: '';
	display: block;
	width: 6px;
	height: 10px;
	border: solid #fff;
	border-width: 0 2px 2px 0;
	transform: scale(0.5) rotate(45deg);
	transition: all 0.35s cubic-bezier(0.3, -.41, .19, 2), opacity 0.3s;
}
.checkbox input:checked + label span.checkbox-icon {
	border-color: #66676b;
	background-color: #66676b;
}
.checkbox input:checked + label span.checkbox-icon:after {
	transform: scale(1) rotate(45deg);
	opacity: 1;
}
