.utf-payment-tab-trigger > label {
	cursor: pointer;
	display: block;
	position: relative;
	top: 0;
	padding: 15px 0;
	top: 0px;
	margin-bottom: 0;
	font-weight: 500;
}
.utf-payment-tab-trigger > label::before {
	transition: 0.2s;
	box-sizing: border-box;
	background-color: #fff;
	border: 2px solid #d0d0d0;
	border-radius: 100%;
	content: "";
	display: inline-block;
	margin-right: 10px;
	position: relative;
	top: 3px;
	width: 18px;
	height: 18px;
}
.utf-payment-tab-trigger > label::after {
	background: #fff;
	border-radius: 50%;
	content: "";
	display: inline-block;
	position: absolute;
	left: 5px;
	top: 24px;
	width: 8px;
	height: 8px;
	transition: 0.2s;
	transform: scale(0);
}
.utf-payment-tab-trigger > input:checked ~ label::before {
	border-color: #66676b;
}
.utf-payment-tab-trigger > input:checked ~ label::after {
	background-color: #66676b;
	transform: scale(1);
}
