/*-----------------------------------
 Street View Button
------------------------------------- */
#streetView, #geoLocation, #scrollEnabling {
	background-color: #fff;
	color: #333;
	cursor: pointer;
	border-radius: 4px;
	margin: 5px 15px;
	transition: 0.3s;
	box-shadow: 0px 2px 4px 0 rgba(0, 0, 0, 0.1);
	position: absolute;
	top: 10px;
	left: 5px;
	z-index: 99;
	font-size: 14px;
	line-height: 21px;
	padding: 8px 14px;
	font-weight: 500;
}
#geoLocation:hover, #streetView:hover, #scrollEnabling:hover, #scrollEnabling.enabled {
	background-color: #66676b;
	color: #fff;
}
#streetView:before {
	content: "\e974";
	font-family: "Material-Icons";
	font-size: 18px;
	top: 3px;
	position: relative;
	margin-right: 7px;
	margin-left: -1px;
	line-height: 0;
}
