/* ---------------------------------- */
/* Dashboard Navigation
------------------------------------- */
.utf-dashboard-nav {
	display: block;
	z-index: 11;
	padding-bottom: 30px;
}
a.utf-dashboard-responsive-trigger-item {
	display: none;
}
.utf-dashboard-nav ul {
	list-style: none;
	padding: 0;
	margin:0;
}
.utf-dashboard-nav ul:first-child {
	margin: 15px 0 0 0;
	border-top:1px solid rgba(0, 0, 0, 0.05);
}
.utf-dashboard-nav ul:before {
	content: attr(data-submenu-title);
	padding: 0 35px;
	margin-bottom:0px;
	display: block;
	color: #66676b;
	font-weight: 600;
	font-size: 14px;
}
.utf-dashboard-nav ul li {
	display: block;
	border-left: 4px solid transparent;
	transition: 0.3s;
	line-height: 25px;
	font-size: 15px;
	margin-bottom:-1px;
	border-bottom:1px solid rgba(255, 255, 255, 0.05);	
}
.utf-dashboard-nav ul li a {
	color: #ffffff;
	display: block;
	padding: 12px 25px;
	transition: 0.3s;
	cursor: pointer;
	position: relative;
    text-decoration: none;
}
.utf-dashboard-nav ul li a i {
	padding: 8px;
	width: 32px;
	height: 32px;
	line-height: 28px;
	font-size: 18px;
	color: #ffffff;
	transition: 0.3s;
	position: relative;
	top: 0px;
	background: rgba(255, 255, 255, 0.08);
	text-align: center;
	vertical-align: middle;
	border-radius: 20px;
	left: -8px;
}
.utf-dashboard-nav ul li.active-submenu a i, .utf-dashboard-nav ul li.active a i, .utf-dashboard-nav ul li:hover a i {
	color: #66676b;
}
.utf-dashboard-nav ul li.active-submenu, .utf-dashboard-nav ul li.active, .utf-dashboard-nav ul li:hover {
	border-color: #66676b;
	background-color: rgba(102,103,107,0.04);
}
.utf-dashboard-nav ul li.active, .utf-dashboard-nav ul li:hover, .utf-dashboard-nav ul li.active-submenu {
	border-bottom:1px solid rgba(255, 255, 255, 0.05) !important;
}
.utf-dashboard-nav ul li.active-submenu a, .utf-dashboard-nav ul li:hover a, .utf-dashboard-nav ul li.active a {
	color: #008c76ff !important;
}
.dashboard-profile-box {
    margin: 10px 0 0;
	padding-bottom:8px;
    border-bottom: 1px solid #454545;
	float:left;
	width:100%;
}
.dashboard-profile-box > span {
    display: block;
    text-align: center;
}
.dashboard-profile-box img {
    margin-bottom: 0px;
    border-radius: 100px;
}
.dashboard-profile-box .fullname {
    color: #fff;
	display: block;
	font-weight: 600;
	font-size: 16px;
}
.dashboard-profile-box .user-role {
    color: #a1a1a1;
	font-size:14px;
	font-weight: 600;
}
.dashboard-profile-box > span.avatar-img{
	float: left;
	width: 100px;
	height: auto;
	padding:10px 17px 10px 20px;
}
.dashboard-profile-box .user-profile-text{
	float:left;
	margin-top:14px;
	width:160px;
}
.dashboard-profile-box > span {
    display: block;
    text-align: left;
	padding-left: 90px;
}
.utf-dashboard-nav ul li span.nav-tag {
	display: inline-block;
	font-weight: 700;
	height: 20px;
	width: 20px;
	line-height: 20px;
	text-align: center;
	color: #fff;
	font-size: 11px;
	background-color: #66676b;
	border-radius: 50%;
	position: relative;
	margin: 0 0 0 4px;
	top: 0px;
}
.utf-dashboard-nav ul li ul {
	padding: 0;
	margin: 0;
	visibility: hidden;
	max-height: 0;
	opacity: 0;
	line-height: 0;
	transition: 0.3s;
	pointer-events: none;
}
.utf-dashboard-nav ul li.active-submenu ul {
	visibility: visible;
	padding: 0;
	opacity: 1;
	max-height: 200px;
	pointer-events: all;
}
.utf-dashboard-nav ul li ul li {
	background-color: transparent;
	border: none;
	padding-left: 25px;
	position: relative;
	margin: 0;
	border-top:1px solid rgba(255, 255, 255, 0.05) !important;
}
.utf-dashboard-nav ul li ul li a {
	padding: 8px 0;	
}
.utf-dashboard-nav ul li ul li:hover {
	background-color: rgba(255, 255, 255, 0.08);
	border-bottom:0 !important;
}
