	color: #666;
}
.fun-fact-icon i.icon-material-outline-gavel {
	transform: scale(0.91);
}
.fun-fact-text {
	margin-top: 15px;
	width:100%;
	text-align:center;
}
.fun-fact span {
	font-size: 15px;
	color: #888;
	line-height: 22px;
}
.fun-fact h4 {
	font-size: 38px;
